@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;500;700&display=swap);
* {
  margin: 0;
  padding: 0;
}

:root {
  --theme-background: #6accba;
  --disabled-button: #eceff1;
  --answer-border: #e4eaee;
  --font-color: #7B99A;
  --disabled-font-color: #858788;
}

body {
  background-color: #6accba;
  background-color: var(--theme-background);
  font-family: 'Montserrat', sans-serif;
}

button {
  font-family: 'Montserat', sans-serif;
  font-size: 17px;
  font-weight: bold;
}

#root {
    display: flex;
    justify-content: center;
    height: 100vh;
    width: 100%;
    align-items: center;
}


.hidden{display:none}.checker{width:450px;background-color:white;height:800px;align-self:auto;border-radius:10px;box-shadow:0px 10px 20px #888;font-size:16px;font-family:'Montserrat';font-weight:700;color:#3f6072}.checker .card-header,.checker .card-action{height:100px}.checker .card-content{height:calc(100% - 200px);flex-direction:column;display:flex;align-items:center}.checker .card-content .question{padding:20px;margin-bottom:20px;text-align:left;width:89%;font-size:22px;font-weight:900;color:#555;position:relative;height:40%;display:flex;align-items:flex-end}.checker .card-content .question::before{content:'';background-color:#fae4e8;width:34px;height:2px;position:absolute;bottom:0}.checker .card-content .choice{display:flex;justify-content:space-around;width:100%}.checker .card-content .choice button{width:40%;height:50px;border-radius:30px;background-color:transparent;border:2px solid var(--answer-border);color:var(--theme-background)}.checker .card-content .choice button.selected{background-color:var(--theme-background);border-color:var(--theme-background);position:relative;color:white}.checker .card-content .outcome{margin:0 30px;font-weight:400}.checker .card-content .outcome button{background-color:var(--theme-background);color:white;width:100%;border-radius:8px;border:none;margin-top:1em;position:relative}.checker .card-action{display:flex;padding:5px}.checker .card-action button{align-self:center;width:90%;background:var(--theme-background);border-radius:7px;min-height:50px;border:none;margin:0 auto;position:relative;color:white}.checker .card-action button:disabled{background:var(--disabled-button);color:var(--disabled-font-color)}.checker .card-action button.restart{background:transparent;color:var(--theme-background);font-weight:200;text-decoration:underline}.checker .card-action button.restart::after{content:none}.row{position:relative}.row .header-action{position:absolute;width:20px;height:20px;bottom:0}.row .header-action:before{content:url(//xopo.github.io/heartburn-checker/static/media/ic-arrow-left-green.a00650a9.svg);width:41px;height:32px;position:absolute;background:white;display:flex;align-items:center;justify-content:center;-webkit-transform:translateY(-13px);transform:translateY(-13px);font-size:38px}.row .header-title{width:100%;text-align:center;height:50px;display:flex;align-items:flex-end;justify-content:center;margin-bottom:10px;font-size:1em}.selected::after{content:url(//xopo.github.io/heartburn-checker/static/media/ic-checkmark.e0da07c5.svg);background-color:white;width:30px;height:30px;position:absolute;border-radius:50%;top:8px;right:9px;display:flex;align-items:center;justify-content:center}.checker .card-action button::after,.book-meeting::after{content:url(//xopo.github.io/heartburn-checker/static/media/ic-arrow-right-white.d2ca8b6d.svg);width:12px;height:18px;position:absolute;border-radius:50%;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);right:15px;display:flex;justify-content:center}.checker .card-action button:disabled::after,.book-meeting:disabled::after{content:url(//xopo.github.io/heartburn-checker/static/media/ic-arrow-right-gray.451f87b6.svg)}.card-header ul{display:flex;width:100%;background:pink;margin:0;padding:0;list-style:none}.card-header ul li{flex:1 1;background-color:#eee;height:2px;color:transparent}.card-header ul li.history{background-color:var(--theme-background)}@media screen and (max-width: 700px){.checker{width:90%;height:90%}}

